.card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #fff;
  padding: 15px;
  border: 1px solid #ddd;
  border-radius: 8px;
  width: 250px;
  margin: 10px;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.1);
  word-wrap: break-word;
  overflow-wrap: break-word;
  min-height: 200px;
}

.content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  /* gap: 10px; */
}

.cardtitle {
  font-size: 18px;
  font-weight: bold;
  padding-bottom: 7px;
  padding-left: 3px;
  border-bottom: 3px solid rgb(146, 146, 241);
}

.cardcontent {
  font-size: 16px;
  margin-top: 0px;
}

.cardinput,
.cardtextarea {
  width: 100%;
  padding: 8px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-sizing: border-box;
  min-height: 30px;
  margin-bottom: 10px;
}

.cardtextarea {
  height: 80px;
}

.cardbuttons {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  margin-top: 10px;
}

.cardbutton {
  padding: 8px 12px;
  font-size: 14px;
  cursor: pointer;
  border: none;
  border-radius: 5px;
}

.cardbutton.edit {
  background-color: #2196f3;
  color: white;
}

.cardbutton.delete {
  background-color: #f44336;
  color: white;
}
