.form {
    margin: auto;
    width: 90%;
    max-width: 500px;
    display: flex;
    flex-direction: column;
    /* gap: 10px; */
    background-color: rgba(255, 255, 255, 0.861);
    /* background-color: #fff; */
    border-radius: 10px;
    box-shadow: 0px 8px 8px rgba(0, 0, 0, 0.1);
  }
  
  .forminput,
  .formtextarea {
    margin: 20px;
    padding: 10px;
    font-size: 16px;
    min-height: 25px;
    background-color: aliceblue;
    border-radius: 5px;
    border: none;
    box-shadow: 2px 3px 3px 0px gray;
  }
  .formtextarea{
    min-height: 80px;
    margin-top: 0px;
  }

  .forminput:hover,
  .formtextarea:hover{
    box-shadow: 4px 5px 7px 0px rgb(135, 133, 133)
  }
  
  .formbutton {
    padding: 10px;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-weight: 700;
    font-size: large;
    width: 40%;
    align-self: center;
    margin-bottom: 15px;
  }
  
  .formbutton:hover{
    background-color: #278e2b;
  }